import React, { Component } from 'react';
import CircularProgress from '@material-ui/core/CircularProgress';
import './stylesheet.css'
import { Typography } from '@material-ui/core';
import {
    Link
  } from "react-router-dom";
 


const CheckedifLoaded = (props) => {
        if(props.data.isLoaded === true){
            return ( 
                <div className="listContainer">
                    {
                        props.data.items.map(item => {
                            let match = 'hide';
                            if(item.title.toLowerCase().match(window.location.pathname.split("/").pop())){
                                match = 'show';
                            }
                            else{
                                match = 'hide';
                            }
                            return(
                                <Link to={`/detail/${item.id}`} key={item.id} className={match}>
                                    <div className={`productContainer`} onClick={props.click} >
                                        <div className="productImageContainer">
                                            <img className="productImage" alt={item.title} src={(item.img)}></img>
                                        </div>
                                        <Typography variant="h5" key={'title' + item.id} className="title">{item.title}</Typography>
                                        <Typography variant="h6" key={'price' + item.id} className="price">${item.price}</Typography>
                                    </div>
                                </Link>
                                )
                        })
                    }
                </div>
            )
        }
    else{
        
        return(
            <div className="modlabel">
                <CircularProgress className='spinner' />
            </div>
        )
    }
}


class List extends Component{

    constructor(props) {
        // console.log(this.props.match.params.search)
        super(props);
        this.state = {
            items: [],
            isLoaded: false,
            redirect: false,
            id: -1,
            search: this.props.match.params.search
        };
    }

    componentDidMount(){
        this.fetchItems();
      }

    
    fetchItems = () => {
        fetch('https://my-json-server.typicode.com/tdmichaelis/json-api/products')
        .then(rsp => rsp.json())
        .then(allItems => {
          this.allItems = allItems;
          this.setState({items: allItems, isLoaded: true, redirect: false})
        })
    }

    render() {

            return(
                <CheckedifLoaded data={this.state} click={this.handleClick} search={window.location.pathname} />
            )
            
    }

}

 
export default List